<template>
    <v-app>
        <v-main>
            <v-container>
                <p>社員情報登録画面</p>
                <v-text-field label="社員ID" clearable v-model="inputEmpId"></v-text-field>
                <v-text-field label="社員名" clearable v-model="inputEmpName"></v-text-field>

            </v-container>
            <v-row justify="center">
                <v-col cols="auto">
                    <v-btn v-on:click="INSERT">登録</v-btn>
                </v-col>
            </v-row>

                <div>
                    <router-link to="/">Home</router-link>
                    <router-link to="/about">About</router-link>
                </div>

            <v-container>
                <v-data-table :headers="headers" :items="desserts" :items-per-page="5" class="elevation-1" :footer-props="{
                              showFirstLastPage: true,
                              firstIcon: 'mdi-arrow-collapse-left',
                              lastIcon: 'mdi-arrow-collapse-right',
                              prevIcon: 'mdi-minus',
                              nextIcon: 'mdi-plus'
                              }">
                    <template v-slot:item="{ item }">
                        <tr>
                            <td class="text-start" :style="{'color': 'black'}">{{ item['社員ID'] }}</td>
                            <td class="text-start" :style="{'color': 'black'}">{{ item['社員名'] }}</td>
                        </tr>
                    </template>
                </v-data-table>
            </v-container>

        </v-main>
    </v-app>
</template>

<style type="text/css">
    p {
            font-family: 'Times New Roman', Times, serif;
    }
</style>

<script>
    import axios from 'axios';
    export default {
        data() {
            return {
                jsonData: "",
                headers: [
                    { text: '社員ID' },
                    { text: '社員名' },
                ],
                desserts: [],
                inputEmpId: "",
                inputEmpName: "",
            }
        },
        methods: {
            async INSERT() {
                try {
                    const response = await axios.get('/api/data');
                    this.message = response.data;
                } catch (error) {
                    console.error('Error:', error);
                }
            }
        }
    }
</script>
